.asset-edit-page {
  max-width: 1000px;
  margin: 0 auto;
}

.section-card {
  background: #fff;
  border-radius: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  border: 1px solid #e5e7eb;
  padding: 2rem;
  margin-bottom: 1.5rem;
  transition: all 0.2s ease-in-out;
}
.section-card:hover {
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}

.section-title {
  font-size: 1.25rem;
  font-weight: 600;
  margin: 0;
  color: #1f2937;
}

.input {
  width: 100%;
  border-radius: 0.5rem;
  border: 1px solid #d1d5db;
  padding: 0.75rem 0.875rem;
  font-size: 1rem;
  background: #ffffff;
  transition: all 0.2s ease-in-out;
}
.input::placeholder {
  color: #9ca3af;
}

.input:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
  background: #ffffff;
}

.input.border-red-300 {
  border-color: #fca5a5;
  box-shadow: 0 0 0 3px rgba(239, 68, 68, 0.1);
}

@media (max-width: 768px) {
  .asset-create-page {
    padding: 1rem;
  }
  .section-card {
    padding: 1.5rem;
    margin-bottom: 1rem;
  }
  .grid-cols-2,
  .grid-cols-3 {
    grid-template-columns: 1fr;
  }
}
.custom-dropdown .dropdown-button {
  transition: all 0.2s ease-in-out;
}
.custom-dropdown .dropdown-button:hover {
  border-color: #9ca3af;
}
.custom-dropdown .dropdown-button:focus {
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}
.custom-dropdown .dropdown-menu {
  animation: slideDown 0.2s ease-out;
}
.custom-dropdown .dropdown-item {
  transition: all 0.15s ease-in-out;
}
.custom-dropdown .dropdown-item:hover {
  background-color: #eff6ff;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.rotate-180 {
  transform: rotate(180deg);
}

.transition-transform {
  transition: transform 0.2s ease-in-out;
}

:host ::ng-deep .flatpickr-calendar {
  border-radius: 0.5rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  border: 1px solid #e5e7eb;
  font-family: inherit;
}

:host ::ng-deep .flatpickr-day {
  border-radius: 0.375rem;
}
:host ::ng-deep .flatpickr-day:hover {
  background-color: #eff6ff;
  border-color: #3b82f6;
}
:host ::ng-deep .flatpickr-day.selected {
  background-color: #3b82f6;
  border-color: #3b82f6;
}
:host ::ng-deep .flatpickr-day.today {
  border-color: #3b82f6;
  color: #3b82f6;
  font-weight: 600;
}

:host ::ng-deep .flatpickr-months {
  background-color: #f8fafc;
  border-radius: 0.5rem 0.5rem 0 0;
}

:host ::ng-deep .flatpickr-current-month {
  color: #1f2937;
  font-weight: 600;
}

:host ::ng-deep .flatpickr-weekday {
  color: #6b7280;
  font-weight: 500;
}

:host ::ng-deep .flatpickr-input {
  cursor: pointer;
}

:host ::ng-deep .flatpickr-input:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.slider-input {
  -webkit-appearance: none;
  appearance: none;
  background: transparent;
  outline: none;
  z-index: 20;
}

.slider-input::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: transparent;
  border: 2px solid transparent;
  cursor: pointer;
  position: relative;
  z-index: 30;
}

.slider-input::-moz-range-thumb {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: transparent;
  border: 2px solid transparent;
  cursor: pointer;
  box-shadow: none;
}

.slider-input:focus {
  outline: none;
}
